<template>
  <div class="scan-container">
    <van-uploader :after-read="afterRead" >
      <van-button icon="plus" type="primary">上传</van-button>
    </van-uploader>
   <h3>
    {{result}}
   </h3>
  </div>
</template>
<script>
import { uploadPictureAPI } from '@/api'
export default {
  name: 'ScanA',
  components: {},
  data() {
    return {
      result:""
    };
  },
  computed: {},
  watch: {},
  props: {},
  methods: {
    async afterRead(file) {
      const fd = new FormData()
      fd.append('multipartFile', file.file)
      const {data} = await uploadPictureAPI(fd)
      this.result = data
    },
  },
  created() {},
}
</script>

